import React, { useState, useEffect } from "react";
import {
  Box,
  Container,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  IconButton,
  Pagination,
  Typography,
  Skeleton,
} from "@mui/material";
import { Edit } from "@mui/icons-material";
import { fetchOrders, updateOrder } from "../api/OrderAPI";
import AdminOrderModal from "../components/AdminOrderModal";

function AdminOrderList() {
  const [orders, setOrders] = useState([]);
  const [pagination, setPagination] = useState({
    total_pages: 1,
    current_page: 1,
  });
  const [loading, setLoading] = useState(false);

  // Modal 
  const [modalOpen, setModalOpen] = useState(false);
  const [modalTitle, setModalTitle] = useState("");
  const [currentOrder, setCurrentOrder] = useState(null);

  const statusMap = {
    pending: "",
    processing: "",
    shipped: "",
    completed: "",
    cancelled: "",
  };

  // 
  const getOrders = async (page = 1) => {
    setLoading(true);
    const result = await fetchOrders(page);
    if (!result.success) {
      alert(result.message || "");
      window.location.href = "/login";
      return;
    }
    setOrders(result.orders);
    setPagination(result.pagination);
    setLoading(false);
  };

  useEffect(() => {
    getOrders(1);
  }, []);

  const handlePageChange = (event, page) => {
    getOrders(page);
  };

  //  Modal
  const handleOpenEdit = (order) => {
    setCurrentOrder(order);
    setModalTitle("");
    setModalOpen(true);
  };

  //  Modal
  const handleCloseModal = () => {
    setModalOpen(false);
    setCurrentOrder(null);
  };

  // Modal 
  const handleSubmit = async (orderData) => {
    const result = await updateOrder(currentOrder.id, orderData);
    if (result.success) {
      setModalOpen(false);
      setTimeout(() => {
        alert("");
        getOrders(pagination.current_page);
      }, 200);
    } else {
      alert(result.message || "");
    }
  };

  return (
    <Container maxWidth="lg">
      <Box sx={{ my: 4 }}>
        {/*  skeleton */}
        {loading ? (
          <Skeleton variant="text" width={200} height={40} sx={{ mb: 2 }} />
        ) : (
          <Typography variant="h4" gutterBottom>
            
          </Typography>
        )}

        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                {loading ? (
                  <>
                    <TableCell>
                      <Skeleton variant="text" width={80} />
                    </TableCell>
                    <TableCell>
                      <Skeleton variant="text" width={100} />
                    </TableCell>
                    <TableCell>
                      <Skeleton variant="text" width={120} />
                    </TableCell>
                    <TableCell>
                      <Skeleton variant="text" width={80} />
                    </TableCell>
                    <TableCell align="right">
                      <Skeleton variant="text" width={60} />
                    </TableCell>
                    <TableCell align="center">
                      <Skeleton variant="text" width={80} />
                    </TableCell>
                  </>
                ) : (
                  <>
                    <TableCell></TableCell>
                    <TableCell></TableCell>
                    <TableCell></TableCell>
                    <TableCell></TableCell>
                    <TableCell align="right"></TableCell>
                    <TableCell align="center"></TableCell>
                  </>
                )}
              </TableRow>
            </TableHead>
            <TableBody>
              {loading ? (
                Array.from(new Array(5)).map((_, index) => (
                  <TableRow key={index}>
                    <TableCell>
                      <Skeleton variant="text" width="80%" />
                    </TableCell>
                    <TableCell>
                      <Skeleton variant="text" width="90%" />
                    </TableCell>
                    <TableCell>
                      <Skeleton variant="text" width="90%" />
                    </TableCell>
                    <TableCell>
                      <Skeleton variant="text" width="70%" />
                    </TableCell>
                    <TableCell align="right">
                      <Skeleton variant="text" width="60%" />
                    </TableCell>
                    <TableCell align="center">
                      <Skeleton
                        variant="circular"
                        width={40}
                        height={40}
                        sx={{ display: "inline-block", mx: 0.5 }}
                      />
                      <Skeleton
                        variant="circular"
                        width={40}
                        height={40}
                        sx={{ display: "inline-block", mx: 0.5 }}
                      />
                    </TableCell>
                  </TableRow>
                ))
              ) : orders.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={6} align="center">
                    
                  </TableCell>
                </TableRow>
              ) : (
                orders.map((order) => (
                  <TableRow key={order.id}>
                    <TableCell>{order.id}</TableCell>
                    <TableCell>{order.customerName}</TableCell>
                    <TableCell>{order.phone}</TableCell>
                    <TableCell>
                      {statusMap[order.status] || order.status}
                    </TableCell>
                    <TableCell align="right">${order.total}</TableCell>
                    <TableCell align="center">
                      <IconButton
                        color="primary"
                        onClick={() => handleOpenEdit(order)}
                      >
                        <Edit />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                ))
              )}
            </TableBody>
          </Table>
        </TableContainer>

        <Box sx={{ mt: 3, display: "flex", justifyContent: "center" }}>
          {loading ? (
            <Skeleton variant="rectangular" width={200} height={40} />
          ) : (
            <Pagination
              count={pagination.total_pages}
              page={pagination.current_page}
              onChange={handlePageChange}
              color="primary"
            />
          )}
        </Box>

        {/*  Modal */}
        <AdminOrderModal
          open={modalOpen}
          onClose={handleCloseModal}
          title={modalTitle}
          initialData={currentOrder}
          onSubmit={handleSubmit}
        />
      </Box>
    </Container>
  );
}

export default AdminOrderList;
