import React from "react";
import { Container, Box, Typography } from "@mui/material";
import Grid2 from "@mui/material/Grid2";
import LooksOneIcon from "@mui/icons-material/LooksOne";
import LooksTwoIcon from "@mui/icons-material/LooksTwo";
import Looks3Icon from "@mui/icons-material/Looks3";
import Looks4Icon from "@mui/icons-material/Looks4";

const features = [
  {
    icon: <LooksOneIcon sx={{ fontSize: 40, mb: 1, color: "#fff" }} />,
    title: "",
    description:
      "",
  },
  {
    icon: <LooksTwoIcon sx={{ fontSize: 40, mb: 1, color: "#fff" }} />,
    title: "",
    description:
      "",
  },
  {
    icon: <Looks3Icon sx={{ fontSize: 40, mb: 1, color: "#fff" }} />,
    title: "",
    description: "",
  },
  {
    icon: <Looks4Icon sx={{ fontSize: 40, mb: 1, color: "#fff" }} />,
    title: "",
    description: "",
  },
];

function FeatureSection() {
  return (
    <>
      {/*  */}
      <Box
        sx={{
          width: "100%", // 
          position: "relative", // 
          bgcolor: "#73b839", // 
          py: 8, // 
        }}
      >
        <Container maxWidth="lg" sx={{ py: 8 }}>
          {/*  */}
          <Typography
            variant="h4"
            component="h2"
            align="center"
            gutterBottom
            color="#fff"
          >
            
          </Typography>
          {/* 2x2 Grid2 gap spacing */}
          <Grid2 container spacing={3} flexWrap="wrap">
            {features.map(({ icon, title, description }, index) => (
              <Grid2
                key={index}
                size={{ xs: 12, md: 6 }}
                sx={{ display: "flex", justifyContent: "center" }} // 
              >
                <Box
                  sx={{
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "center",
                    textAlign: "center",
                    px: 2,
                    maxWidth: 400, // 
                    width: "100%",
                  }}
                >
                  {icon}
                  <Typography variant="h6" gutterBottom color="#fff">
                    {title}
                  </Typography>
                  <Typography variant="body2" color="#fff">
                    {description}
                  </Typography>
                </Box>
              </Grid2>
            ))}
          </Grid2>
        </Container>
      </Box>
    </>
  );
}
export default FeatureSection;
