import React from "react";
import FeatureSection from "../components/FeatureSection";
import CarouselSection from "../components/Carousel";
import LeftImageRightTextSection from "../components/LeftImageRightText";
import ParallaxSection from "../components/Parallax";
import LeftImg from "../assets/img/home-tea-left-image-right-text.jpg";
import RightImg from "../assets/img/home-tea-right-image-left-text.jpg";
import Grid2 from "@mui/material/Grid2";
import { Typography } from "@mui/material";

function Home() {
  return (
    <>
      <CarouselSection />
      <FeatureSection />
      <LeftImageRightTextSection
        reverse={false}
        imgSrc={LeftImg}
        title=""
        description=""
        btnText=""
        btnLink="/about-us"
      >
        {/*  Grid2 container  children */}
        <Grid2 container spacing={3}>
          <Grid2 size={6}>
            <Typography variant="h6" gutterBottom>
              
            </Typography>
            <Typography variant="body2">
              
            </Typography>
          </Grid2>
          <Grid2 size={6}>
            <Typography variant="h6" gutterBottom>
              
            </Typography>
            <Typography variant="body2">
              60%77%
            </Typography>
          </Grid2>
        </Grid2>
      </LeftImageRightTextSection>
      <ParallaxSection />
      <LeftImageRightTextSection
        reverse={true}
        imgSrc={RightImg}
        title=""
        description="500"
        btnText=""
        btnLink="/contact-us"
      >
        <Grid2 container spacing={3}>
          <Grid2 size={6}>
            <Typography variant="h6" gutterBottom>
              
            </Typography>
            <Typography variant="body2">
              
            </Typography>
          </Grid2>
          <Grid2 size={6}>
            <Typography variant="h6" gutterBottom>
              
            </Typography>
            <Typography variant="body2">
              
            </Typography>
          </Grid2>
        </Grid2>
      </LeftImageRightTextSection>
    </>
  );
}

export default Home;
