import React, { useState, useEffect } from "react";
import {
  Box,
  Button,
  Container,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  IconButton,
  Pagination,
  Typography,
  Skeleton,
} from "@mui/material";
import { Edit, Delete } from "@mui/icons-material";
import {
  fetchProducts,
  updateProduct,
  createProduct,
  deleteProduct,
} from "../api/ProductAPI";
import AdminProductModal from "../components/AdminProductModal";

function AdminProductList() {
  const [products, setProducts] = useState([]);
  const [pagination, setPagination] = useState({
    total_pages: 1,
    current_page: 1,
  });
  const [loading, setLoading] = useState(false);

  // Modal 
  const [modalOpen, setModalOpen] = useState(false);
  const [modalTitle, setModalTitle] = useState("");
  const [currentProduct, setCurrentProduct] = useState(null);

  // 
  const getProducts = async (page = 1) => {
    setLoading(true);
    const result = await fetchProducts(page);
    if (!result.success) {
      alert(result.message || "");
      window.location.href = "/login";
      return;
    }
    setProducts(result.products);
    setPagination(result.pagination);
    setLoading(false);
  };

  useEffect(() => {
    getProducts(1);
  }, []);

  const handlePageChange = (event, page) => {
    getProducts(page);
  };

  // Modal
  const handleOpenCreate = () => {
    setCurrentProduct(null); // 
    setModalTitle("");
    setModalOpen(true);
  };

  // Modal
  const handleOpenEdit = (product) => {
    setCurrentProduct(product);
    setModalTitle("");
    setModalOpen(true);
  };

  //
  const handleDelete = async (productId) => {
    if (!window.confirm("")) return;

    const result = await deleteProduct(productId);
    if (result.success) {
      alert("");
      getProducts(pagination.current_page); // 
    } else {
      alert(result.message || "");
    }
  };

  //  Modal
  const handleCloseModal = () => {
    setModalOpen(false);
    setCurrentProduct(null);
  };

  // Modal 
  const handleSubmit = async (productData) => {
    let result;
    if (currentProduct && currentProduct.id) {
      // 
      result = await updateProduct(currentProduct.id, productData);
    } else {
      // 
      result = await createProduct(productData);
    }

    setModalOpen(false); //  Modal

    setTimeout(() => {
      if (result.success) {
        alert(
          currentProduct && currentProduct.id ? "" : ""
        );
        getProducts(pagination.current_page);
      } else {
        alert(
          result.message ||
            (currentProduct && currentProduct.id
              ? ""
              : "")
        );
      }
    }, 200); //  200ms Modal 
  };

  return (
    <Container maxWidth="lg">
      <Box sx={{ my: 4 }}>
        {/*  skeleton  */}
        <Box
          sx={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            mb: 2,
          }}
        >
          {loading ? (
            <>
              <Skeleton variant="text" width={200} height={40} />
              <Skeleton variant="rectangular" width={120} height={36} />
            </>
          ) : (
            <>
              <Typography variant="h4" gutterBottom>
                
              </Typography>
              <Button
                variant="contained"
                color="primary"
                onClick={handleOpenCreate}
              >
                
              </Button>
            </>
          )}
        </Box>
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                {loading ? (
                  <>
                    <TableCell>
                      <Skeleton variant="text" width={80} />
                    </TableCell>
                    <TableCell>
                      <Skeleton variant="text" width={100} />
                    </TableCell>
                    <TableCell align="right">
                      <Skeleton variant="text" width={60} />
                    </TableCell>
                    <TableCell align="right">
                      <Skeleton variant="text" width={60} />
                    </TableCell>
                    <TableCell align="center">
                      <Skeleton variant="text" width={80} />
                    </TableCell>
                  </>
                ) : (
                  <>
                    <TableCell></TableCell>
                    <TableCell></TableCell>
                    <TableCell align="right"></TableCell>
                    <TableCell align="right"></TableCell>
                    <TableCell align="center"></TableCell>
                  </>
                )}
              </TableRow>
            </TableHead>
            <TableBody>
              {loading ? (
                Array.from(new Array(5)).map((_, index) => (
                  <TableRow key={index}>
                    <TableCell>
                      <Skeleton variant="text" width="80%" />
                    </TableCell>
                    <TableCell>
                      <Skeleton variant="text" width="90%" />
                    </TableCell>
                    <TableCell align="right">
                      <Skeleton variant="text" width="60%" />
                    </TableCell>
                    <TableCell align="right">
                      <Skeleton variant="text" width="60%" />
                    </TableCell>
                    <TableCell align="center">
                      <Skeleton
                        variant="circular"
                        width={40}
                        height={40}
                        sx={{ display: "inline-block", mx: 0.5 }}
                      />
                      <Skeleton
                        variant="circular"
                        width={40}
                        height={40}
                        sx={{ display: "inline-block", mx: 0.5 }}
                      />
                    </TableCell>
                  </TableRow>
                ))
              ) : products.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={5} align="center">
                    
                  </TableCell>
                </TableRow>
              ) : (
                products.map((product) => (
                  <TableRow key={product.id}>
                    <TableCell>{product.category}</TableCell>
                    <TableCell>{product.title}</TableCell>
                    <TableCell align="right">${product.origin_price}</TableCell>
                    <TableCell align="right">${product.price}</TableCell>
                    <TableCell align="center">
                      <IconButton
                        color="primary"
                        onClick={() => handleOpenEdit(product)}
                      >
                        <Edit />
                      </IconButton>
                      <IconButton
                        color="error"
                        onClick={() => handleDelete(product.id)}
                      >
                        <Delete />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                ))
              )}
            </TableBody>
          </Table>
        </TableContainer>
        <Box sx={{ mt: 3, display: "flex", justifyContent: "center" }}>
          {loading ? (
            <Skeleton variant="rectangular" width={200} height={40} />
          ) : (
            <Pagination
              count={pagination.total_pages}
              page={pagination.current_page}
              onChange={handlePageChange}
              color="primary"
            />
          )}
        </Box>

        {/*  Modal/ */}
        <AdminProductModal
          open={modalOpen}
          onClose={handleCloseModal}
          title={modalTitle}
          initialData={currentProduct}
          onSubmit={handleSubmit}
        />
      </Box>
    </Container>
  );
}

export default AdminProductList;
